import * as React from "react";
import Paper from "@mui/material/Paper";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TablePagination from "@mui/material/TablePagination";
import TableRow from "@mui/material/TableRow";
import { useAppDispatch, useAppSelector } from "../store-reducers/store";
import SchoolIcon from "@mui/icons-material/School";
import FilterAltOffIcon from "@mui/icons-material/FilterAltOff";
import {
  deletePacksTC,
  getPacksTC,
  idPacksAC,
  newPacksTC,
  pagePacksAC,
  pageCountPacksAC,
  sortPacksAC,
  searchPacksAC,
  updatePacksTC,
  removeFilterAC,
} from "../store-reducers/packs-reducer";
import { Navigate, NavLink } from "react-router-dom";
import { useEffect, useState } from "react";
import row from "./../IMG/Polygon 2.png";
import s from "./AllPacks.module.css";
import { SuperInput } from "../features/SuperInput";
import { Button } from "@mui/material";
import { Range } from "../features/Range";
import DeleteForeverIcon from "@mui/icons-material/DeleteForever";
import BorderColorIcon from "@mui/icons-material/BorderColor";
import { idCardsAC } from "../store-reducers/cards-reducer";
import { v1 } from "uuid";
import { ModalPacks } from "../features/ModalPacks";
import { DeleteModal } from "../features/DeleteModal";

interface Column {
  id: "name" | "Cards" | "Updated" | "Created" | "Actions";
  label: string;
  minWidth?: number;
  align?: "center";
  display?: "flex";
}

const columns: readonly Column[] = [
  { id: "name", label: "Name", minWidth: 170 },
  { id: "Cards", label: "Cards", minWidth: 100 },
  {
    id: "Updated",
    label: "Last Updated",
    minWidth: 170,
    align: "center",
  },
  {
    id: "Created",
    label: "Created by",
    minWidth: 170,
    align: "center",
  },
  {
    id: "Actions",
    label: "Actions",
    minWidth: 170,
    align: "center",
    display: "flex",
  },
];

interface Data {
  name: string;
  Cards: number;
  Updated: string;
  Created: string;
  Actions: any;
  id: string;
}

function createData(
  name: any,
  Cards: number,
  Updated: string,
  Created: string,
  Actions: any,
  id: string
): Data {
  return { name, Cards, Updated, Created, Actions, id };
}

export function AllPacks() {
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const pageReducer = useAppSelector((state) => state.packsReducer.page);
  const [page, setPage] = useState(pageReducer !== 1 ? pageReducer : 0);
  const min = useAppSelector((state) => state.packsReducer.minCardsCount);
  const max = useAppSelector((state) => state.packsReducer.maxCardsCount);
  const [value, setValue] = useState<number[]>([min, max]);
  const packs = useAppSelector((state) => state.packsReducer.cardPacks);
  const sort = useAppSelector((state) => state.packsReducer.sort);
  const id = useAppSelector((state) => state.registrationAuthLoginReducer.id);
  const myPackId = useAppSelector((state) => state.packsReducer.id);
  const search = useAppSelector((state) => state.packsReducer.search);
  const packTotalCount = useAppSelector(
    (state) => state.packsReducer.cardPacksTotalCount
  );
  const auth = useAppSelector(
    (state) => state.registrationAuthLoginReducer.auth
  );
  const status = useAppSelector((state) => state.errorReducer.status);
  const pageCount = useAppSelector((state) => state.packsReducer.pageCount);
  const dispatch = useAppDispatch();

  useEffect(() => {
    if (!auth) {
      return;
    }
    dispatch(getPacksTC());
  }, [pageCount, dispatch, pageReducer, sort, min, max, myPackId, search]);
  if (!auth) {
    return <Navigate to="/signin" />;
  }

  const removeFilter = () => {
    setValue([0, 110]);
    dispatch(removeFilterAC());
  };

  const HandleClickSort = () => {
    if (sort === "0updated") {
      dispatch(sortPacksAC({ sort: "1updated" }));
    } else {
      dispatch(sortPacksAC({ sort: "0updated" }));
    }
  };
  const handleChangePage = (event: unknown, newPage: number) => {
    if (status === "loading") {
      return;
    }
    setPage(newPage);
    dispatch(pagePacksAC({ page: newPage + 1 }));
  };
  const handleChangeRowsPerPage = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    setRowsPerPage(+event.target.value);
    dispatch(pageCountPacksAC({ pageCount: +event.target.value }));
    setPage(0);
    dispatch(pagePacksAC({ page: 0 }));
  };

  const rows = packs.map((el) =>
    createData(
      <div style={{ display: "flex", alignItems: "center" }}>
        <img
          src={el.deckCover}
          style={{ marginRight: "10px" }}
          width={50}
          alt=""
        />{" "}
        <NavLink
          className={s.sox}
          onClick={() => dispatch(idCardsAC({ id: el._id }))}
          to={"/cards/" + el._id}
        >
          {el.name}
        </NavLink>
      </div>,
      el.cardsCount,
      el.updated.slice(0, 10),
      el.user_name,
      [
        <Button
          variant={"contained"}
          key={v1()}
          disabled={status === "loading" || el.cardsCount === 0}
        >
          <NavLink
            className={s.sd}
            onClick={() => dispatch(idCardsAC({ id: el._id }))}
            to={"/cards/" + el._id + "/learn"}
          >
            <SchoolIcon />
          </NavLink>
        </Button>,
        <ModalPacks
          key={v1()}
          nameAction={"Edit pack"}
          value={el.name}
          name={<BorderColorIcon />}
          id={el._id}
          callbackTwo={updatePacksTC}
        />,
        <DeleteModal
          key={v1()}
          packName={el.name}
          packDel={"Delete Pack"}
          name={<DeleteForeverIcon />}
          callback={deletePacksTC}
          id={el._id}
        />,
      ],
      el.user_id
    )
  );
  const HandleChangeMyAndAllPacks = (id: string) => {
    dispatch(pagePacksAC({ page: 1 }));
    setPage(0);
    dispatch(idPacksAC({ id }));
  };

  return search.length === 0 && min === 0 && max === 110 ? (
    packs.length === 0 ? (
      <>
        <div>
          <div className={s.glav}>
            <span className={s.packs}>Packs list</span>
            <div className={s.btn}>
              <div className={s.str}>
                <span>Show packs cards</span>
              </div>
              <Button
                disabled={status === "loading"}
                onClick={() => HandleChangeMyAndAllPacks(id)}
                style={{ padding: "15px 50px" }}
                variant={myPackId !== "" ? "contained" : "outlined"}
              >
                My
              </Button>
              <Button
                disabled={status === "loading"}
                onClick={() => HandleChangeMyAndAllPacks("")}
                style={{ padding: "15px 50px" }}
                variant={myPackId === "" ? "contained" : "outlined"}
              >
                All
              </Button>
            </div>
            <ModalPacks
              nameAction={"Add new pack"}
              name={"Add new pack"}
              callbackOne={newPacksTC}
            />
          </div>

          <span className={s.spanZero}>
            This packs is empty. Click add new pack to fill this packs
          </span>
        </div>
      </>
    ) : (
      <>
        <div className={s.glav}>
          <span className={s.packs}>Packs list</span>
          <ModalPacks
            nameAction={"Add new pack"}
            name={"Add new pack"}
            callbackOne={newPacksTC}
          />
        </div>
        <div className={s.panel}>
          <div className={s.search}>
            <span className={s.str}>Search</span>
            <SuperInput txt={search} setPage={setPage} search={searchPacksAC} />
          </div>
          <div className={s.btn}>
            <div className={s.str}>
              <span>Show packs cards</span>
            </div>
            <Button
              disabled={status === "loading"}
              onClick={() => HandleChangeMyAndAllPacks(id)}
              style={{ padding: "15px 50px" }}
              variant={myPackId !== "" ? "contained" : "outlined"}
            >
              My
            </Button>
            <Button
              disabled={status === "loading"}
              onClick={() => HandleChangeMyAndAllPacks("")}
              style={{ padding: "15px 50px" }}
              variant={myPackId === "" ? "contained" : "outlined"}
            >
              All
            </Button>
          </div>
          <div>
            <span className={s.str}>Number of cards</span>
            <Range setPage={setPage} value={value} setValue={setValue} />
          </div>
          <div className={s.filter}>
            <Button
              onClick={removeFilter}
              disabled={status === "loading"}
              variant={"contained"}
            >
              <FilterAltOffIcon />
            </Button>
          </div>
        </div>
        <Paper sx={{ width: "80%", overflow: "hidden", margin: "auto" }}>
          <TableContainer className={s.pagination} sx={{ maxHeight: 600 }}>
            <Table stickyHeader aria-label="sticky table">
              <TableHead>
                <TableRow>
                  {columns.map((column) => (
                    <TableCell
                      onClick={
                        column.id === "Updated" ? HandleClickSort : undefined
                      }
                      key={v1()}
                      align={column.align}
                      style={{
                        minWidth: column.minWidth,
                        cursor: "pointer",
                        backgroundColor: " #EFEFEF",
                        fontWeight: "bold",
                      }}
                    >
                      {column.label}
                      {column.id === "Updated" ? (
                        <img
                          className={sort === "1updated" ? s.row : undefined}
                          src={row}
                          alt=""
                        />
                      ) : undefined}
                    </TableCell>
                  ))}
                </TableRow>
              </TableHead>
              <TableBody>
                {rows.map((row) => {
                  return (
                    <TableRow hover role="checkbox" key={v1()}>
                      {columns.map((column) => {
                        const value =
                          row.id !== id
                            ? row[column.id] === row.Actions
                              ? row.Actions[0]
                              : row[column.id]
                            : row[column.id];
                        return (
                          <TableCell
                            className={s.inline}
                            key={v1()}
                            align={column.align}
                          >
                            {value}
                          </TableCell>
                        );
                      })}
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
          <TablePagination
            rowsPerPageOptions={[10, 25, 50]}
            component="div"
            count={packTotalCount}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </Paper>
      </>
    )
  ) : packs.length !== 0 ? (
    <>
      <div className={s.glav}>
        <span className={s.packs}>Packs list</span>
        <ModalPacks
          nameAction={"Add new pack"}
          name={"Add new pack"}
          callbackOne={newPacksTC}
        />
      </div>
      <div className={s.panel}>
        <div className={s.search}>
          <span className={s.str}>Search</span>
          <SuperInput txt={search} setPage={setPage} search={searchPacksAC} />
        </div>
        <div className={s.btn}>
          <div className={s.str}>
            <span>Show packs cards</span>
          </div>
          <Button
            disabled={status === "loading"}
            onClick={() => HandleChangeMyAndAllPacks(id)}
            style={{ padding: "15px 50px" }}
            variant={myPackId !== "" ? "contained" : "outlined"}
          >
            My
          </Button>
          <Button
            disabled={status === "loading"}
            onClick={() => HandleChangeMyAndAllPacks("")}
            style={{ padding: "15px 50px" }}
            variant={myPackId === "" ? "contained" : "outlined"}
          >
            All
          </Button>
        </div>
        <div>
          <span className={s.str}>Number of cards</span>
          <Range setPage={setPage} value={value} setValue={setValue} />
        </div>
        <div className={s.filter}>
          <Button
            onClick={removeFilter}
            disabled={status === "loading"}
            variant={"contained"}
          >
            <FilterAltOffIcon />
          </Button>
        </div>
      </div>
      <Paper sx={{ width: "80%", overflow: "hidden", margin: "auto" }}>
        <TableContainer className={s.pagination} sx={{ maxHeight: 600 }}>
          <Table stickyHeader aria-label="sticky table">
            <TableHead>
              <TableRow>
                {columns.map((column) => (
                  <TableCell
                    onClick={
                      column.id === "Updated" ? HandleClickSort : undefined
                    }
                    key={v1()}
                    align={column.align}
                    style={{
                      minWidth: column.minWidth,
                      cursor: "pointer",
                      backgroundColor: " #EFEFEF",
                      fontWeight: "bold",
                    }}
                  >
                    {column.label}
                    {column.id === "Updated" ? (
                      <img
                        className={sort === "1updated" ? s.row : undefined}
                        src={row}
                        alt=""
                      />
                    ) : undefined}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map((row) => {
                return (
                  <TableRow hover role="checkbox" key={v1()}>
                    {columns.map((column) => {
                      const value =
                        row.id !== id
                          ? row[column.id] === row.Actions
                            ? row.Actions[0]
                            : row[column.id]
                          : row[column.id];
                      return (
                        <TableCell
                          className={s.inline}
                          key={v1()}
                          align={column.align}
                        >
                          {value}
                        </TableCell>
                      );
                    })}
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[10, 25, 50]}
          component="div"
          count={packTotalCount}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
    </>
  ) : (
    <>
      <div className={s.glav}>
        <span className={s.packs}>Packs list</span>
        <ModalPacks
          nameAction={"Add new pack"}
          name={"Add new pack"}
          callbackOne={newPacksTC}
        />
      </div>
      <div className={s.panel}>
        <div className={s.search}>
          <span className={s.str}>Search</span>
          <SuperInput txt={search} setPage={setPage} search={searchPacksAC} />
        </div>
        <div className={s.btn}>
          <div className={s.str}>
            <span>Show packs cards</span>
          </div>
          <Button
            disabled={status === "loading"}
            onClick={() => HandleChangeMyAndAllPacks(id)}
            style={{ padding: "15px 50px" }}
            variant={myPackId !== "" ? "contained" : "outlined"}
          >
            My
          </Button>
          <Button
            disabled={status === "loading"}
            onClick={() => HandleChangeMyAndAllPacks("")}
            style={{ padding: "15px 50px" }}
            variant={myPackId === "" ? "contained" : "outlined"}
          >
            All
          </Button>
        </div>
        <div>
          <span className={s.str}>Number of cards</span>
          <Range setPage={setPage} value={value} setValue={setValue} />
        </div>
        <div className={s.filter}>
          <Button
            onClick={removeFilter}
            disabled={status === "loading"}
            variant={"contained"}
          >
            <FilterAltOffIcon />
          </Button>
        </div>
      </div>
      <span className={s.spanZero}>
        There is no matching packs for the given filter
      </span>
    </>
  );
}
