import React, { useEffect, useState } from "react";
import Card from "@mui/material/Card";
import s from "./CardLogin.module.css";
import { useAppDispatch, useAppSelector } from "../store-reducers/store";
import { Navigate, NavLink, useParams } from "react-router-dom";
import {
  CardType,
  getCardsTC,
  gradeTC,
  searchQCardsAC,
} from "../store-reducers/cards-reducer";
import {
  Button,
  FormControlLabel,
  FormLabel,
  Radio,
  RadioGroup,
} from "@mui/material";
import ArrowCircleLeftOutlinedIcon from "@mui/icons-material/ArrowCircleLeftOutlined";
import Skeleton from "@mui/material/Skeleton";

const grades = [
  { grade: 1, name: "не знал" },
  { grade: 2, name: "забыл" },
  { grade: 3, name: "долго думал" },
  { grade: 4, name: "перепутал" },
  { grade: 5, name: "знал" },
];

const getCard = (cards: CardType[]) => {
  console.log(cards);
  const sum = cards.reduce(
    (acc, card) => acc + (6 - card.grade) * (6 - card.grade),
    0
  );
  const rand = Math.random() * sum;
  const res = cards.reduce(
    (acc: { sum: number; id: number }, card, i) => {
      const newSum = acc.sum + (6 - card.grade) * (6 - card.grade);
      return { sum: newSum, id: newSum < rand ? i : acc.id };
    },
    { sum: 0, id: -1 }
  );
  return cards[res.id + 1];
};

export function Learn() {
  const cards = useAppSelector((state) => state.cardsReducer.cards);
  const status = useAppSelector((state) => state.errorReducer.status);
  const [value, setValue] = useState("");
  const auth = useAppSelector(
    (state) => state.registrationAuthLoginReducer.auth
  );
  const pack = useAppSelector((state) => state.packsReducer.cardPacks);
  const [show, setShow] = useState(false);
  const url = useParams();
  const packName = pack.find((el) => url.id === el._id);
  const dispatch = useAppDispatch();
  console.log(status);
  const [cardState, setCardState] = useState(getCard(cards));
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    console.log("here");
    if (cards) {
      setCardState(getCard(cards));
    }
  }, [cards]);

  useEffect(() => {
    if (!auth) {
      return;
    }
    if (url.id) {
      dispatch(getCardsTC(url.id));
    }
  }, [url.id]);
  if (!auth) {
    return <Navigate to={"/signin"} />;
  }
  const grader = grades.find((el) => el.name === value);
  const onclickHandler = async () => {
    setLoading(true);
    await dispatch(
      gradeTC({ id: cardState._id, grade: grader?.grade ? grader.grade : 0 })
    );
    setShow(false);
    setLoading(false);
  };

  const grade = grades.map((el) => (
    <FormControlLabel
      key={el.grade}
      value={el.name}
      control={<Radio />}
      label={el.name}
    />
  ));
  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setValue((event.target as HTMLInputElement).value);
  };

  return (
    <>
      <NavLink
        onClick={() => dispatch(searchQCardsAC({ text: "" }))}
        className={s.back}
        to={"/cards/" + packName?._id}
      >
        <ArrowCircleLeftOutlinedIcon />
        <span>Back to Cards List</span>
      </NavLink>
      <Card className={s.card} sx={{ minWidth: 275, maxWidth: 413 }}>
        <h2>Learn '{packName?.name}'</h2>
        <div>
          <b>Question:</b>{" "}
          {loading ? (
            <Skeleton animation="wave" />
          ) : cardState?.question === "no question" ? (
            cardState?.questionImg ? (
              <img src={cardState?.questionImg} width={100} alt="" />
            ) : (
              cardState?.question
            )
          ) : (
            cardState?.question
          )}
        </div>
        <span className={s.sp}>
          Количество попыток ответов на вопрос: {cardState?.shots}
        </span>
        <Button
          onClick={() => setShow(true)}
          className={s.btn}
          variant={"contained"}
        >
          Show answer
        </Button>
        {show ? (
          <div>
            <div>
              <b>Answer:</b>{" "}
              {cardState?.answer === "no answer" ? (
                cardState?.answerImg ? (
                  <img src={cardState?.answerImg} width={100} alt="" />
                ) : (
                  cardState?.answer
                )
              ) : (
                cardState?.answer
              )}
            </div>
            <div>
              <FormLabel id="demo-radio-buttons-group-label">
                Rate yourself:
              </FormLabel>
              <RadioGroup
                value={value}
                onChange={handleChange}
                aria-labelledby="demo-radio-buttons-group-label"
                defaultValue="female"
                name="radio-buttons-group"
              >
                {grade}
              </RadioGroup>
            </div>
            <Button onClick={onclickHandler} variant={"contained"}>
              Next
            </Button>
          </div>
        ) : undefined}
      </Card>
    </>
  );
}
