import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit";
import {
  Data,
  DataLogin,
  registrationAuthLoginAPI,
} from "../API/regstr-auth-login-api";
import { initialAC, statusAC } from "./error-reducer";
import { HandleError } from "../utils/error";

export type InitialStateType = {
  registration: boolean;
  auth: boolean;
  img: string;
  name: string;
  email: undefined | string;
  newPass: boolean;
  id: string;
};
const initialState: InitialStateType = {
  registration: false,
  auth: false,
  img: "https://a.d-cd.net/OLn9cEYLYEkeQwmAwA_IyKhglqk-960.jpg",
  name: "",
  email: undefined,
  newPass: false,
  id: "",
};

const slice = createSlice({
  name: "registration-auth-login",
  initialState,
  reducers: {
    avatarProfileAC(state, action: PayloadAction<{ avatar: string }>) {
      state.img = action.payload.avatar;
    },
  },
  extraReducers: (builder) => {
    builder.addCase(registrationTC.fulfilled, (state, action) => {
      state.registration = action.payload.value;
    });
    builder.addCase(loginTC.fulfilled, (state, action) => {
      state.auth = action.payload.value;
      state.name = action.payload.name;
      state.img = action.payload.avatar;
      state.id = action.payload.id;
    });
    builder.addCase(logOutTC.fulfilled, (state) => {
      state.auth = false;
      state.img = "https://a.d-cd.net/OLn9cEYLYEkeQwmAwA_IyKhglqk-960.jpg";
      state.name = "";
    });
    builder.addCase(newPassTC.fulfilled, (state, action) => {
      state.newPass = action.payload.pass;
    });
    builder.addCase(nameTC.fulfilled, (state, action) => {
      state.name = action.payload.name;
      state.img = action.payload.avatar;
    });
    builder.addCase(avatarTC.fulfilled, (state, action) => {
      state.img = action.payload.avatar;
    });
    builder.addCase(authTC.fulfilled, (state, action) => {
      state.auth = true;
      state.name = action.payload.name;
      state.email = action.payload.email;
      state.id = action.payload.id;
      state.img = action.payload.img;
    });
  },
});

export const { avatarProfileAC } = slice.actions;
export const registrationAuthLoginReducer = slice.reducer;

export const authTC = createAsyncThunk(
  "registration-auth-login/auth",
  async (param, { dispatch, rejectWithValue }) => {
    try {
      dispatch(statusAC({ status: "loading" }));
      let response = await registrationAuthLoginAPI.auth();
      dispatch(statusAC({ status: "success" }));
      dispatch(initialAC({ initial: true }));
      return {
        name: response.data.name,
        email: response.data.email,
        id: response.data._id,
        img: response.data.avatar
          ? response.data.avatar
          : "https://a.d-cd.net/OLn9cEYLYEkeQwmAwA_IyKhglqk-960.jpg",
      };
    } catch (e) {
      dispatch(statusAC({ status: "success" }));
      dispatch(initialAC({ initial: true }));
      return rejectWithValue(null);
    }
  }
);
export const registrationTC = createAsyncThunk(
  "registration-auth-login/registration",
  async (param: Data, { dispatch, rejectWithValue }) => {
    try {
      dispatch(statusAC({ status: "loading" }));
      await registrationAuthLoginAPI.registration(param);
      dispatch(statusAC({ status: "success" }));
      return { value: true };
    } catch (e) {
      HandleError(e, dispatch);
      return rejectWithValue(null);
    }
  }
);
export const loginTC = createAsyncThunk(
  "registration-auth-login/login",
  async (param: DataLogin, { dispatch, rejectWithValue }) => {
    try {
      dispatch(statusAC({ status: "loading" }));
      let response = await registrationAuthLoginAPI.login(param);
      dispatch(statusAC({ status: "success" }));
      return {
        value: true,
        name: response.data.name,
        avatar: response.data.avatar
          ? response.data.avatar
          : "https://a.d-cd.net/OLn9cEYLYEkeQwmAwA_IyKhglqk-960.jpg",
        id: response.data._id,
      };
    } catch (e) {
      HandleError(e, dispatch);
      return rejectWithValue(null);
    }
  }
);
export const logOutTC = createAsyncThunk(
  "registration-auth-login/logout",
  async (param, { dispatch, rejectWithValue }) => {
    try {
      dispatch(statusAC({ status: "loading" }));
      await registrationAuthLoginAPI.logOut();
      dispatch(statusAC({ status: "success" }));
    } catch (e) {
      HandleError(e, dispatch);
      return rejectWithValue(null);
    }
  }
);
export const forgotTC = createAsyncThunk(
  "registration-auth-login/forgot",
  async (email: string, { dispatch, rejectWithValue }) => {
    try {
      dispatch(statusAC({ status: "loading" }));
      await registrationAuthLoginAPI.forgot(email);
      dispatch(statusAC({ status: "success" }));
    } catch (e) {
      HandleError(e, dispatch);
      return rejectWithValue(null);
    }
  }
);
export const newPassTC = createAsyncThunk(
  "registration-auth-login/new-pass",
  async (
    param: { pass: string; token: string },
    { dispatch, rejectWithValue }
  ) => {
    try {
      dispatch(statusAC({ status: "loading" }));
      await registrationAuthLoginAPI.newPass(param.pass, param.token);
      dispatch(statusAC({ status: "success" }));
      return { pass: true };
    } catch (e) {
      HandleError(e, dispatch);
      return rejectWithValue(null);
    }
  }
);
export const nameTC = createAsyncThunk(
  "registration-auth-login/new-name",
  async (
    param: { name: string; avatar?: string },
    { dispatch, rejectWithValue }
  ) => {
    try {
      dispatch(statusAC({ status: "loading" }));
      const res = await registrationAuthLoginAPI.name(param.name, param.avatar);
      dispatch(statusAC({ status: "success" }));
      return {
        name: res.data.updatedUser.name,
        avatar: res.data.updatedUser.avatar,
      };
    } catch (e) {
      HandleError(e, dispatch);
      return rejectWithValue(null);
    }
  }
);

export const avatarTC = createAsyncThunk(
  "registration-auth-login/avatar",
  async (param: { avatar: string }, { dispatch, rejectWithValue }) => {
    try {
      dispatch(statusAC({ status: "loading" }));
      const res = await registrationAuthLoginAPI.avatar(param.avatar);
      dispatch(statusAC({ status: "success" }));
      return {
        avatar: res.data.updatedUser.avatar,
      };
    } catch (e) {
      HandleError(e, dispatch);
      return rejectWithValue(null);
    }
  }
);
