import * as React from "react";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import { Checkbox, FormControlLabel, TextField } from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import s from "../components/AllPacks.module.css";
import { ChangeEvent, useState } from "react";
import { useAppDispatch, useAppSelector } from "../store-reducers/store";
import { InputTypeFile } from "./InputTypeFile";

const style = {
  position: "absolute" as "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};
type PropsType = {
  callbackOne?: (param: { name: string; value: boolean }) => any;
  callbackTwo?: (param: { id: string; name: string; value: boolean }) => any;
  id?: string;
  name: any;
  value?: string;
  nameAction: string;
};

export function ModalPacks(props: PropsType) {
  console.log("qwfqefqefqefqe");
  const status = useAppSelector((state) => state.errorReducer.status);
  const avatar = useAppSelector((state) => state.packsReducer.avatar);
  const [open, setOpen] = React.useState(false);
  const coverImg = useAppSelector((state) => state.packsReducer.cardPacks);
  const cover = coverImg.find((el) => el._id === props.id);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const [text, setText] = useState(props.value ? props.value : "");
  const [value, setValue] = useState(false);
  const dispatch = useAppDispatch();
  const OnChangeHandler = (
    e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    setText(e.currentTarget.value);
  };
  const onChangeHandlerCheckbox = (e: ChangeEvent<HTMLInputElement>) => {
    setValue(e.currentTarget.checked);
  };
  const onClickHandler = () => {
    if (props.callbackOne) {
      dispatch(props.callbackOne({ name: text, value: value }));
    }
    if (props.callbackTwo) {
      dispatch(
        props.callbackTwo({
          id: props.id ? props.id : "s",
          name: text,
          value: value,
        })
      );
    }
    setOpen(false);
    setText("");
  };
  return (
    <div>
      <Button
        className={s.sd}
        disabled={status === "loading"}
        variant="contained"
        onClick={handleOpen}
      >
        {props.name}
      </Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box className={s.modal} sx={style}>
          <div className={s.modalTitle}>
            <Typography id="modal-modal-title" variant="h6" component="h2">
              {props.nameAction}
            </Typography>
            <CloseIcon onClick={handleClose} className={s.modalClose} />
          </div>
          <TextField
            onChange={OnChangeHandler}
            value={text}
            className={s.input}
            id="standard-basic"
            label="Name pack"
            variant="standard"
          />
          <div>
            <InputTypeFile />
          </div>
          <div>
            <img src={avatar ? avatar : cover?.deckCover} width={200} alt="" />
          </div>
          <div className={s.checkbox}>
            <FormControlLabel
              control={
                <Checkbox value={value} onChange={onChangeHandlerCheckbox} />
              }
              label="Private pack"
            />
          </div>

          <div className={s.modalTitle}>
            <Button onClick={handleClose} variant="outlined">
              Cancel
            </Button>
            <Button onClick={onClickHandler} variant="contained">
              Save
            </Button>
          </div>
        </Box>
      </Modal>
    </div>
  );
}
