import axios from "axios";
import { errorAC, initialAC, statusAC } from "../store-reducers/error-reducer";
import { Dispatch } from "@reduxjs/toolkit";
export const HandleError = (e: any, dispatch: Dispatch) => {
  const err = e as Error;
  if (axios.isAxiosError(e)) {
    dispatch(errorAC({ error: e.response?.data.error }));
    dispatch(statusAC({ status: "success" }));
    dispatch(initialAC({ initial: true }));
  } else if (err) {
    dispatch(statusAC({ status: "success" }));
    dispatch(initialAC({ initial: true }));
  }
};
