import * as React from "react";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import {
  FormControl,
  InputLabel,
  Select,
  SelectChangeEvent,
  TextField,
} from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import s from "../components/AllPacks.module.css";
import { useAppDispatch, useAppSelector } from "../store-reducers/store";
import MenuItem from "@mui/material/MenuItem";
import { ChangeEvent, useState } from "react";
import { uploadHandler } from "../utils/FuncBase64";
import { answerIMGAC, questionIMGAC } from "../store-reducers/cards-reducer";

const style = {
  position: "absolute" as "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

type PropsType = {
  iconName: any;
  callbackOne?: (param: { question: string; answer: string }) => any;
  callbackTwo?: (param: {
    id: string;
    question: string;
    answer: string;
  }) => any;
  name: string;
  editQuestion?: string;
  editAnswer?: string;
  id?: string;
};

export function ModalCard(props: PropsType) {
  const status = useAppSelector((state) => state.errorReducer.status);
  const questionIMG = useAppSelector((state) => state.cardsReducer.questionIMG);
  const answerIMG = useAppSelector((state) => state.cardsReducer.answerIMG);
  const [open, setOpen] = React.useState(false);

  const [age, setAge] = React.useState("");
  const [text, setText] = React.useState<any>("no");

  const handleChange = (event: SelectChangeEvent) => {
    setAge(event.target.value as string);
  };
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const [question, setQuestion] = useState(
    props.editQuestion ? props.editQuestion : ""
  );
  const [answer, setAnswer] = useState(
    props.editAnswer ? props.editAnswer : ""
  );
  const dispatch = useAppDispatch();
  const OnChangeHandlerQuestion = (
    e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    setQuestion(e.currentTarget.value);
  };
  const onChangeHandlerAnswer = (
    e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    setAnswer(e.currentTarget.value);
  };

  const onClickSubmit = () => {
    if (props.callbackOne) {
      dispatch(props.callbackOne({ question, answer }));
      setOpen(false);
      setQuestion("");
      setAnswer("");
      dispatch(answerIMGAC({ avatar: "" }));
      dispatch(questionIMGAC({ avatar: "" }));
    }
    if (props.callbackTwo) {
      dispatch(
        props.callbackTwo({ id: props.id ? props.id : "", question, answer })
      );
      setOpen(false);
      setQuestion("");
      setAnswer("");
      dispatch(answerIMGAC({ avatar: "" }));
      dispatch(questionIMGAC({ avatar: "" }));
    }
  };

  return (
    <div>
      <Button
        disabled={status === "loading"}
        variant="contained"
        onClick={handleOpen}
      >
        {props.iconName}
      </Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box className={s.modal} sx={style}>
          <div className={s.modalCard}>
            <Typography id="modal-modal-title" variant="h6" component="h2">
              {props.name}
            </Typography>
            <CloseIcon onClick={handleClose} className={s.modalClose} />
          </div>
          <FormControl fullWidth>
            <InputLabel id="demo-simple-select-label">Value</InputLabel>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={age}
              label="Age"
              onChange={handleChange}
            >
              <MenuItem
                defaultChecked
                onClick={() => setText(false)}
                value="Text"
              >
                Text
              </MenuItem>
              <MenuItem onClick={() => setText(true)} value="IMG">
                IMG
              </MenuItem>
            </Select>
          </FormControl>

          {text === "no" ? undefined : text ? (
            <div>
              <label>
                <input
                  type="file"
                  onChange={(e) => uploadHandler(e, dispatch, questionIMGAC)}
                  style={{ display: "none" }}
                />
                <Button
                  variant={"contained"}
                  style={{ marginTop: "20px" }}
                  component="span"
                >
                  Загрузить фотовопрос
                </Button>
              </label>
              <div>
                <img src={questionIMG} width={100} alt="" />
              </div>

              <label>
                <input
                  type="file"
                  onChange={(e) => uploadHandler(e, dispatch, answerIMGAC)}
                  style={{ display: "none" }}
                />
                <Button
                  variant={"contained"}
                  style={{ marginTop: "20px" }}
                  component="span"
                >
                  Загрузить фотответ
                </Button>
              </label>
              <div>
                <img src={answerIMG} width={100} alt="" />
              </div>
            </div>
          ) : (
            <div className={s.textField}>
              <TextField
                onChange={OnChangeHandlerQuestion}
                value={question}
                className={s.nova}
                id="standard-basic"
                label="Question"
                variant="standard"
              />
              <TextField
                onChange={onChangeHandlerAnswer}
                value={answer}
                className={s.input}
                id="standard-basic"
                label="Answer"
                variant="standard"
              />
            </div>
          )}
          <div className={s.modalBtn}>
            <Button onClick={handleClose} variant="outlined">
              Cancel
            </Button>
            <Button onClick={onClickSubmit} variant="contained">
              Save
            </Button>
          </div>
        </Box>
      </Modal>
    </div>
  );
}
