import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit";
import { AppRootStateType } from "./store";
import { statusAC } from "./error-reducer";
import { Cards, registrationAuthLoginAPI } from "../API/regstr-auth-login-api";
import { HandleError } from "../utils/error";

export type CardType = {
  answer: string;
  question: string;
  answerImg: string;
  questionImg: string;
  cardsPack_id: string;
  grade: number;
  shots: number;
  user_id: string;
  created: string;
  updated: string;
  _id: string;
};
export type RespType = {
  cards: Cards[];
  packDeckCover: string;
  cardsTotalCount: number;
  maxGrade: number;
  minGrade: number;
  page: number;
  pageCount: number;
  packUserId: string;
  sort: string;
  searchQ: string;
  searchA: string;
  id: string;
  packName: string;
  questionIMG: string;
  answerIMG: string;
  learnValue: any;
};
const initialState: RespType = {
  cards: [],
  packDeckCover: "",
  cardsTotalCount: 0,
  maxGrade: 6,
  minGrade: 0,
  page: 0,
  pageCount: 10,
  packUserId: "",
  sort: "0grade",
  searchQ: "",
  searchA: "",
  id: "",
  packName: "",
  questionIMG: "",
  answerIMG: "",
  learnValue: {},
};

const slice = createSlice({
  name: "cards",
  initialState,
  reducers: {
    questionIMGAC(state, action: PayloadAction<{ avatar: string }>) {
      state.questionIMG = action.payload.avatar;
    },
    answerIMGAC(state, action: PayloadAction<{ avatar: string }>) {
      state.answerIMG = action.payload.avatar;
    },
    pageCountCardsAC(state, action: PayloadAction<{ pageCount: number }>) {
      state.pageCount = action.payload.pageCount;
    },
    pageCardsAC(state, action: PayloadAction<{ page: number }>) {
      state.page = action.payload.page;
    },
    minMaxCardsAC(state, action: PayloadAction<{ min: number; max: number }>) {
      state.maxGrade = action.payload.min;
      state.maxGrade = action.payload.max;
    },
    sortCardsAC(state, action: PayloadAction<{ sort: string }>) {
      state.sort = action.payload.sort;
    },
    searchQCardsAC(state, action: PayloadAction<{ text: string }>) {
      state.searchQ = action.payload.text;
    },
    searchACardsAC(state, action: PayloadAction<{ text: string }>) {
      state.searchA = action.payload.text;
    },
    idCardsAC(state, action: PayloadAction<{ id: string }>) {
      state.id = action.payload.id;
    },
  },
  extraReducers: (builder) => {
    builder.addCase(getCardsTC.fulfilled, (state, action) => {
      state.cards = action.payload.cards.cards.map((el) => ({ ...el }));
      state.cardsTotalCount = action.payload.cards.cardsTotalCount;
      state.pageCount = action.payload.cards.pageCount;
      state.maxGrade = action.payload.cards.maxGrade;
      state.minGrade = action.payload.cards.minGrade;
      state.packUserId = action.payload.cards.packUserId;
      state.packName = action.payload.cards.packName;
    });
    builder.addCase(gradeTC.fulfilled, (state, action) => {
      state.cards = action.payload;
    });
    builder.addCase(newCardTC.fulfilled, (state, action) => {
      state.cards = action.payload.cards.cards.map((el) => ({ ...el }));
    });
    builder.addCase(deleteCardTC.fulfilled, (state, action) => {
      state.cards = action.payload.cards.cards.map((el) => ({ ...el }));
    });
    builder.addCase(updateCardsTC.fulfilled, (state, action) => {
      state.cards = action.payload.cards.cards.map((el) => ({ ...el }));
    });
  },
});

export const cardsReducer = slice.reducer;

export const {
  pageCountCardsAC,
  pageCardsAC,
  minMaxCardsAC,
  sortCardsAC,
  searchQCardsAC,
  searchACardsAC,
  idCardsAC,
  questionIMGAC,
  answerIMGAC,
} = slice.actions;

export const getCardsTC = createAsyncThunk(
  "cards/get",
  async (id: string, { dispatch, rejectWithValue, getState }) => {
    const state = getState() as AppRootStateType;
    const { pageCount, page, maxGrade, minGrade, sort, searchQ, searchA } =
      state.cardsReducer;
    try {
      dispatch(statusAC({ status: "loading" }));
      const res = await registrationAuthLoginAPI.cards(
        pageCount,
        page,
        sort,
        minGrade,
        maxGrade,
        id,
        searchQ,
        searchA
      );
      dispatch(statusAC({ status: "success" }));
      return { cards: res.data };
    } catch (e) {
      HandleError(e, dispatch);
      return rejectWithValue(null);
    }
  }
);
