export const gradeTC = createAsyncThunk(
  "cards/grade",
  async (
    param: { id: string; grade: number },
    { dispatch, rejectWithValue, getState }
  ) => {
    const state = getState() as AppRootStateType;
    const { id, page, maxGrade, minGrade, sort, searchQ, searchA } =
      state.cardsReducer;

    try {
      dispatch(statusAC({ status: "loading" }));
      const res = await registrationAuthLoginAPI.grade(param.id, param.grade);
      const cardsRes = await registrationAuthLoginAPI.cards(
        110,
        page,
        sort,
        minGrade,
        maxGrade,
        id,
        searchQ,
        searchA
      );
      const cards = cardsRes.data.cards;
      dispatch(statusAC({ status: "success" }));
      return cards.map((el) => {
        const isCardUpdatedByCurrentUser =
          el.user_id === res.data.updatedGrade.user_id;
        const isCardFromCurrentPack =
          el.cardsPack_id === res.data.updatedGrade.cardsPack_id;
        const isCurrentCardUpdated = el._id === res.data.updatedGrade._id;

        if (
          isCardUpdatedByCurrentUser &&
          isCardFromCurrentPack &&
          isCurrentCardUpdated
        ) {
          return { ...el, grade: res.data.updatedGrade.grade };
        }
        return el;
      });
    } catch (e) {
      HandleError(e, dispatch);
      return rejectWithValue(null);
    }
  }
);
