import * as React from "react";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import CloseIcon from "@mui/icons-material/Close";
import s from "../components/AllPacks.module.css";
import { useAppDispatch, useAppSelector } from "../store-reducers/store";
import { idCardsAC } from "../store-reducers/cards-reducer";

const style = {
  position: "absolute" as "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};
type PropsType = {
  packDel?: string;
  cardDel?: string;
  packName?: string;
  cardName?: string;
  name: any;
  id: string;
  callback: (value: string) => any;
};

export function DeleteModal(props: PropsType) {
  const status = useAppSelector((state) => state.errorReducer.status);
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const dispatch = useAppDispatch();

  const onClickHandler = async (value: string) => {
    await dispatch(props.callback(value));
    setOpen(false);
  };

  return (
    <div>
      <Button
        className={s.sd}
        disabled={status === "loading"}
        variant="contained"
        onClick={handleOpen}
      >
        {props.name}
      </Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box className={s.modal} sx={style}>
          <div className={s.modalTitle}>
            <Typography id="modal-modal-title" variant="h6" component="h2">
              {props.packDel ? props.packDel : props.cardDel}
            </Typography>
            <CloseIcon onClick={handleClose} className={s.modalClose} />
          </div>
          <div className={s.typo}>
            <Typography>
              Do you really want to remove{" "}
              <span className={s.p}>
                {props.cardName ? props.cardName : props.packName}
              </span>
              ? All cards will be deleted.
            </Typography>
          </div>
          <div className={s.modalTitle}>
            <Button onClick={handleClose} variant="outlined">
              Cancel
            </Button>
            <Button
              color={"error"}
              onClick={() => onClickHandler(props.id)}
              variant="contained"
            >
              Delete
            </Button>
          </div>
        </Box>
      </Modal>
    </div>
  );
}
