import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit";
import { statusAC } from "./error-reducer";
import { registrationAuthLoginAPI } from "../API/regstr-auth-login-api";
import { AppRootStateType } from "./store";
import { HandleError } from "../utils/error";
import { idCardsAC } from "./cards-reducer";

export type PacksType = {
  created: string;
  deckCover: string;
  grade?: number;
  more_id?: string;
  path?: string;
  private?: boolean;
  rating?: number;
  shots?: number;
  type?: string;
  user_id: string;
  __v?: number;
  _id: string;
  user_name: string;
  cardsCount: number;
  updated: string;
  name: string;
};
export type RespType = {
  cardPacks: PacksType[];
  cardPacksTotalCount: number;
  maxCardsCount: number;
  minCardsCount: number;
  page: number;
  pageCount: number;
  token: string;
  tokenDeathTime: number;
  sort: string;
  search: string;
  id: string;
  avatar: string;
};
const initialState: RespType = {
  cardPacks: [],
  cardPacksTotalCount: 0,
  maxCardsCount: 110,
  minCardsCount: 0,
  page: 0,
  pageCount: 10,
  token: "",
  tokenDeathTime: 0,
  sort: "0updated",
  search: "",
  id: "",
  avatar: "",
};

const slice = createSlice({
  name: "packs",
  initialState,
  reducers: {
    removeFilterAC(state) {
      state.sort = "0updated";
      state.search = "";
      state.minCardsCount = 0;
      state.maxCardsCount = 110;
    },
    pageCountPacksAC(state, action: PayloadAction<{ pageCount: number }>) {
      state.pageCount = action.payload.pageCount;
    },
    pagePacksAC(state, action: PayloadAction<{ page: number }>) {
      state.page = action.payload.page;
    },
    minMaxPacksAC(state, action: PayloadAction<{ min: number; max: number }>) {
      state.minCardsCount = action.payload.min;
      state.maxCardsCount = action.payload.max;
      state.page = 0;
    },
    sortPacksAC(state, action: PayloadAction<{ sort: string }>) {
      state.sort = action.payload.sort;
    },
    searchPacksAC(state, action: PayloadAction<{ text: string }>) {
      state.search = action.payload.text;
    },
    idPacksAC(state, action: PayloadAction<{ id: string }>) {
      state.id = action.payload.id;
    },
    avatarAC(state, action: PayloadAction<{ avatar: string }>) {
      state.avatar = action.payload.avatar;
    },
  },
  extraReducers: (builder) => {
    builder.addCase(getPacksTC.fulfilled, (state, action) => {
      state.cardPacks = action.payload.pack.cardPacks.map((el: PacksType) => ({
        ...el,
        deckCover:
          el.deckCover && el.deckCover !== "url or base 64"
            ? el.deckCover
            : "https://i1.sndcdn.com/avatars-000064006708-xvoch7-t500x500.jpg",
      }));
      state.cardPacksTotalCount = action.payload.pack.cardPacksTotalCount;
      state.pageCount = action.payload.pack.pageCount;
    });
    builder.addCase(deletePacksTC.fulfilled, (state, action) => {
      state.cardPacks = action.payload.pack.cardPacks.map((el: PacksType) => ({
        ...el,
        deckCover:
          el.deckCover && el.deckCover !== "url or base 64"
            ? el.deckCover
            : "https://i1.sndcdn.com/avatars-000064006708-xvoch7-t500x500.jpg",
      }));
      state.cardPacksTotalCount = action.payload.pack.cardPacksTotalCount;
      state.pageCount = action.payload.pack.pageCount;
    });
    builder.addCase(newPacksTC.fulfilled, (state, action) => {
      state.cardPacks = action.payload.pack.cardPacks.map((el: PacksType) => ({
        ...el,
        deckCover:
          el.deckCover && el.deckCover !== "url or base 64"
            ? el.deckCover
            : "https://i1.sndcdn.com/avatars-000064006708-xvoch7-t500x500.jpg",
      }));
      state.cardPacksTotalCount = action.payload.pack.cardPacksTotalCount;
      state.pageCount = action.payload.pack.pageCount;
    });
    builder.addCase(updatePacksTC.fulfilled, (state, action) => {
      state.cardPacks = action.payload.pack.cardPacks.map((el: PacksType) => ({
        ...el,
        deckCover:
          el.deckCover && el.deckCover !== "url or base 64"
            ? el.deckCover
            : "https://i1.sndcdn.com/avatars-000064006708-xvoch7-t500x500.jpg",
      }));
      state.cardPacksTotalCount = action.payload.pack.cardPacksTotalCount;
      state.pageCount = action.payload.pack.pageCount;
    });
  },
});
export const packsReducer = slice.reducer;
export const {
  pageCountPacksAC,
  minMaxPacksAC,
  sortPacksAC,
  searchPacksAC,
  pagePacksAC,
  idPacksAC,
  removeFilterAC,
  avatarAC,
} = slice.actions;

export const getPacksTC = createAsyncThunk(
  "packs/get",
  async (param, { dispatch, rejectWithValue, getState }) => {
    const state = getState() as AppRootStateType;
    const { pageCount, page, maxCardsCount, minCardsCount, sort, search, id } =
      state.packsReducer;
    try {
      dispatch(statusAC({ status: "loading" }));
      const res = await registrationAuthLoginAPI.packs(
        sort,
        pageCount,
        page,
        search,
        minCardsCount,
        maxCardsCount,
        id
      );
      dispatch(statusAC({ status: "success" }));
      return { pack: res.data };
    } catch (e) {
      HandleError(e, dispatch);
      return rejectWithValue(null);
    }
  }
);
export const newPacksTC = createAsyncThunk(
  "packs/new",
  async (
    param: { name: string; value: boolean },
    { dispatch, rejectWithValue, getState }
  ) => {
    const state = getState() as AppRootStateType;
    const {
      avatar,
      pageCount,
      page,
      maxCardsCount,
      minCardsCount,
      sort,
      search,
      id,
    } = state.packsReducer;
    try {
      dispatch(statusAC({ status: "loading" }));
      await registrationAuthLoginAPI.newPacks(param.name, param.value, avatar);
      const res = await registrationAuthLoginAPI.packs(
        sort,
        pageCount,
        page,
        search,
        minCardsCount,
        maxCardsCount,
        id
      );
      dispatch(avatarAC({ avatar: "" }));
      dispatch(statusAC({ status: "success" }));
      return { pack: res.data };
    } catch (e) {
      HandleError(e, dispatch);
      return rejectWithValue(null);
    }
  }
);
export const deletePacksTC = createAsyncThunk(
  "packs/delete",
  async (packId: string, { dispatch, rejectWithValue, getState }) => {
    const state = getState() as AppRootStateType;
    const { pageCount, page, maxCardsCount, minCardsCount, sort, search, id } =
      state.packsReducer;
    try {
      dispatch(statusAC({ status: "loading" }));
      await registrationAuthLoginAPI.deletePacks(packId);
      dispatch(idCardsAC({ id: "" }));
      const res = await registrationAuthLoginAPI.packs(
        sort,
        pageCount,
        page,
        search,
        minCardsCount,
        maxCardsCount,
        id
      );
      dispatch(statusAC({ status: "success" }));
      return { pack: res.data };
    } catch (e) {
      HandleError(e, dispatch);
      return rejectWithValue(null);
    }
  }
);

export const updatePacksTC = createAsyncThunk(
  "packs/update",
  async (
    param: { id: string; name: string; value: boolean },
    { dispatch, rejectWithValue, getState }
  ) => {
    const state = getState() as AppRootStateType;
    const {
      avatar,
      pageCount,
      page,
      maxCardsCount,
      minCardsCount,
      sort,
      search,
      id,
    } = state.packsReducer;
    try {
      dispatch(statusAC({ status: "loading" }));
      await registrationAuthLoginAPI.updatePack(
        param.id,
        param.name,
        param.value,
        avatar
      );
      const res = await registrationAuthLoginAPI.packs(
        sort,
        pageCount,
        page,
        search,
        minCardsCount,
        maxCardsCount,
        id
      );
      dispatch(statusAC({ status: "success" }));
      return { pack: res.data };
    } catch (e) {
      HandleError(e, dispatch);
      return rejectWithValue(null);
    }
  }
);
