import React from "react";
import Button from "@mui/material/Button";
import { useAppDispatch } from "../store-reducers/store";
import { avatarAC } from "../store-reducers/packs-reducer";
import { uploadHandler } from "../utils/FuncBase64";

export const InputTypeFile = () => {
  const dispatch = useAppDispatch();
  return (
    <label>
      <input
        type="file"
        onChange={(e) => uploadHandler(e, dispatch, avatarAC)}
        style={{ display: "none" }}
      />
      <Button
        variant={"contained"}
        style={{ marginTop: "20px" }}
        component="span"
      >
        Загрузить изображение
      </Button>
    </label>
  );
};
