import * as React from "react";
import Box from "@mui/material/Box";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import DriveFileRenameOutlineIcon from "@mui/icons-material/DriveFileRenameOutline";
import IconButton from "@mui/material/IconButton";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import Tooltip from "@mui/material/Tooltip";
import DeleteForeverIcon from "@mui/icons-material/DeleteForever";
import SchoolIcon from "@mui/icons-material/School";
import { ModalPacks } from "./ModalPacks";
import { v1 } from "uuid";
import { deletePacksTC, updatePacksTC } from "../store-reducers/packs-reducer";
import { NavLink, useParams } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "../store-reducers/store";
import { DeleteModal } from "./DeleteModal";
import s from "../components/AllPacks.module.css";
import { idCardsAC } from "../store-reducers/cards-reducer";
import Button from "@mui/material/Button";

export function Menushka() {
  const dispatch = useAppDispatch();
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const packs = useAppSelector((state) => state.packsReducer.cardPacks);
  const id = useAppSelector((state) => state.cardsReducer.id);
  const namePack = packs.find((el) => el._id === id);
  const open = Boolean(anchorEl);
  const url = useParams();
  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <React.Fragment>
      <Box sx={{ display: "flex", alignItems: "center", textAlign: "center" }}>
        <Tooltip title="Account settings">
          <IconButton
            onClick={handleClick}
            size="small"
            sx={{ ml: 2 }}
            aria-controls={open ? "account-menu" : undefined}
            aria-haspopup="true"
            aria-expanded={open ? "true" : undefined}
          >
            <MoreVertIcon sx={{ width: 32, height: 32 }}>M</MoreVertIcon>
          </IconButton>
        </Tooltip>
      </Box>
      <Menu
        anchorEl={anchorEl}
        id="account-menu"
        open={open}
        onClose={handleClose}
        PaperProps={{
          elevation: 0,
          sx: {
            overflow: "visible",
            filter: "drop-shadow(0px 2px 8px rgba(0,0,0,0.32))",
            mt: 1.5,
            "& .MuiAvatar-root": {
              width: 32,
              height: 32,
              ml: -0.5,
              mr: 1,
            },
            "&:before": {
              content: '""',
              display: "block",
              position: "absolute",
              top: 0,
              right: 14,
              width: 10,
              height: 10,
              bgcolor: "background.paper",
              transform: "translateY(-50%) rotate(45deg)",
              zIndex: 0,
            },
          },
        }}
        transformOrigin={{ horizontal: "right", vertical: "top" }}
        anchorOrigin={{ horizontal: "right", vertical: "bottom" }}
      >
        <MenuItem>
          <ModalPacks
            key={v1()}
            nameAction={"Edit pack"}
            value={namePack?.name}
            name={
              <div>
                <DriveFileRenameOutlineIcon /> Edit
              </div>
            }
            id={id}
            callbackTwo={updatePacksTC}
          />
        </MenuItem>
        <MenuItem>
          <DeleteModal
            key={v1()}
            packName={namePack?.name}
            packDel={"Delete Pack"}
            name={
              <div>
                <DeleteForeverIcon /> Delete
              </div>
            }
            callback={deletePacksTC}
            id={id}
          />
        </MenuItem>
        <MenuItem>
          <Button variant={"contained"}>
            <NavLink
              className={s.sd}
              onClick={() => dispatch(idCardsAC({ id: id }))}
              to={"/cards/" + id + "/learn"}
            >
              <div>
                <SchoolIcon /> Learn
              </div>
            </NavLink>
          </Button>
        </MenuItem>
      </Menu>
    </React.Fragment>
  );
}
