import * as React from "react";
import Paper from "@mui/material/Paper";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TablePagination from "@mui/material/TablePagination";
import TableRow from "@mui/material/TableRow";
import { useAppDispatch, useAppSelector } from "../store-reducers/store";
import {
  deleteCardTC,
  newCardTC,
  pageCardsAC,
  pageCountCardsAC,
  searchQCardsAC,
  sortCardsAC,
  updateCardsTC,
} from "../store-reducers/cards-reducer";
import { Navigate, NavLink, useLocation, useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import row from "./../IMG/Polygon 2.png";
import s from "./AllPacks.module.css";
import { SuperInput } from "../features/SuperInput";
import { Button } from "@mui/material";
import DeleteForeverIcon from "@mui/icons-material/DeleteForever";
import BorderColorIcon from "@mui/icons-material/BorderColor";
import { getCardsTC } from "../store-reducers/cards-reducer";
import { HalfRating } from "../features/HalfRating";
import { v1 } from "uuid";
import ArrowCircleLeftOutlinedIcon from "@mui/icons-material/ArrowCircleLeftOutlined";
import { Menushka } from "../features/Menushka";
import { DeleteModal } from "../features/DeleteModal";
import { ModalCard } from "../features/ModalCard";

interface Column {
  id: "question" | "Answer" | "Updated" | "Grade" | "Actions";
  label: string;
  minWidth?: number;
  align?: "center";
}

const columns: readonly Column[] = [
  { id: "question", label: "Question", minWidth: 170 },
  { id: "Answer", label: "Answer", minWidth: 100 },
  {
    id: "Updated",
    label: "Last Updated",
    minWidth: 170,
    align: "center",
  },
  {
    id: "Grade",
    label: "Grade",
    minWidth: 170,
    align: "center",
  },
  {
    id: "Actions",
    label: "Actions",
    minWidth: 170,
    align: "center",
  },
];

interface Data {
  question: string | JSX.Element;
  Answer: string | JSX.Element;
  Updated: string;
  Grade: JSX.Element;
  Actions: JSX.Element[] | undefined;
  id: string;
}

function createData(
  question: string | JSX.Element,
  Answer: string | JSX.Element,
  Updated: string,
  Grade: JSX.Element,
  Actions: JSX.Element[] | undefined,
  id: string
): Data {
  return { question, Answer, Updated, Grade, Actions, id };
}

export function Cards() {
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [page, setPage] = useState(0);

  const cards = useAppSelector((state) => state.cardsReducer.cards);
  const sort = useAppSelector((state) => state.cardsReducer.sort);
  const userId = useAppSelector(
    (state) => state.registrationAuthLoginReducer.id
  );
  const packId = useAppSelector((state) => state.cardsReducer.packUserId);
  const id = useAppSelector((state) => state.cardsReducer.id);
  const searchQ = useAppSelector((state) => state.cardsReducer.searchQ);
  const searchA = useAppSelector((state) => state.cardsReducer.searchA);
  const cardsTotalCount = useAppSelector(
    (state) => state.cardsReducer.cardsTotalCount
  );
  const auth = useAppSelector(
    (state) => state.registrationAuthLoginReducer.auth
  );
  const min = useAppSelector((state) => state.cardsReducer.minGrade);
  const max = useAppSelector((state) => state.cardsReducer.maxGrade);
  const pageReducer = useAppSelector((state) => state.cardsReducer.page);
  const packName = useAppSelector((state) => state.packsReducer.cardPacks);
  const coverImg = useAppSelector((state) => state.packsReducer.cardPacks);
  const url = useParams();
  const cover = coverImg.find((el) => el._id === url.id);
  const namePack = packName.find((el) => el._id === url.id);
  const dispatch = useAppDispatch();
  const location = useLocation();
  useEffect(() => {
    if (id && auth) {
      dispatch(getCardsTC(id));
    }
    return;
  }, [
    rowsPerPage,
    dispatch,
    pageReducer,
    sort,
    min,
    max,
    searchQ,
    searchA,
    id,
  ]);

  if (!auth) {
    return <Navigate to="/signin" state={location} />;
  }

  const HandleClickSort = () => {
    if (sort === "0grade") {
      dispatch(sortCardsAC({ sort: "1grade" }));
    } else {
      dispatch(sortCardsAC({ sort: "0grade" }));
    }
  };

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage);
    dispatch(pageCardsAC({ page: newPage + 1 }));
  };

  const handleChangeRowsPerPage = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    setRowsPerPage(+event.target.value);
    dispatch(pageCountCardsAC({ pageCount: +event.target.value }));
    setPage(0);
    dispatch(pageCardsAC({ page: 0 }));
  };
  const rows = cards.map((el) =>
    createData(
      el.question !== "no question" ? (
        <span style={{ display: "flex", alignItems: "center" }}>
          {el.questionImg ? (
            <img
              style={{ marginRight: "15px" }}
              src={el.questionImg}
              width={50}
              alt=""
            />
          ) : undefined}{" "}
          {el.question}
        </span>
      ) : el.questionImg ? (
        <img src={el.questionImg} width={50} alt="" />
      ) : (
        el.question
      ),
      el.answer !== "no answer" ? (
        <span style={{ display: "flex", alignItems: "center" }}>
          {el.answerImg ? (
            <img
              style={{ marginRight: "15px" }}
              src={el.answerImg}
              width={50}
              alt=""
            />
          ) : undefined}{" "}
          {el.answer}
        </span>
      ) : el.answerImg ? (
        <img src={el.answerImg} width={50} alt="" />
      ) : (
        el.answer
      ),
      el.updated.slice(0, 10),
      <HalfRating grade={el.grade} />,
      [
        <div className={s.buton}>
          <ModalCard
            editQuestion={el.question}
            editAnswer={el.answer}
            id={el._id}
            callbackTwo={updateCardsTC}
            key={v1()}
            iconName={<BorderColorIcon />}
            name={"Edit card"}
          />
          <DeleteModal
            key={v1()}
            cardName={el.question}
            cardDel={"Delete Card"}
            name={<DeleteForeverIcon />}
            callback={deleteCardTC}
            id={el._id}
          />
        </div>,
      ],
      el.user_id
    )
  );

  return !id ? (
    <>
      <NavLink
        onClick={() => dispatch(searchQCardsAC({ text: "" }))}
        className={s.back}
        to="/"
      >
        <ArrowCircleLeftOutlinedIcon /> <span>Back to Packs List</span>
      </NavLink>
      <span className={s.spanZero}>Package removed successfully</span>
    </>
  ) : searchQ.length === 0 ? (
    cards.length === 0 ? (
      <>
        <NavLink
          onClick={() => dispatch(searchQCardsAC({ text: "" }))}
          className={s.back}
          to="/"
        >
          <ArrowCircleLeftOutlinedIcon /> <span>Back to Packs List</span>
        </NavLink>
        <div className={s.glav}>
          {" "}
          {packId === userId ? (
            <div>
              <span className={s.packs}>
                {namePack?.name} <Menushka />
              </span>{" "}
              <img src={cover?.deckCover} width={150} alt="" />
            </div>
          ) : (
            <div>
              <span className={s.packs}>{namePack?.name}</span>{" "}
              <img src={cover?.deckCover} width={150} alt="" />
            </div>
          )}
          {packId === userId ? (
            <ModalCard
              iconName={"Add New Card"}
              name={"Add New Card"}
              callbackOne={newCardTC}
            />
          ) : undefined}
        </div>
        <span className={s.spanZero}>No Cards</span>
      </>
    ) : (
      <>
        <NavLink
          onClick={() => dispatch(searchQCardsAC({ text: "" }))}
          className={s.back}
          to="/"
        >
          <ArrowCircleLeftOutlinedIcon /> <span>Back to Packs List</span>
        </NavLink>
        <div className={s.glav}>
          {" "}
          {packId === userId ? (
            <div>
              <span className={s.packs}>
                {namePack?.name}
                <Menushka />
              </span>{" "}
              <img src={cover?.deckCover} width={150} alt="" />
            </div>
          ) : (
            <div>
              <span className={s.packs}>{namePack?.name}</span>{" "}
              <img src={cover?.deckCover} width={150} alt="" />
            </div>
          )}
          {packId === userId ? (
            <ModalCard
              iconName={"Add New Card"}
              name={"Add New Card"}
              callbackOne={newCardTC}
            />
          ) : (
            <Button variant="contained">
              <NavLink className={s.nav} to={"/cards/" + url.id + "/learn"}>
                Learn to pack
              </NavLink>
            </Button>
          )}
        </div>
        <div className={s.panel}>
          <div className={s.search}>
            <span className={s.str}>Search</span>
            <SuperInput
              txt={searchQ}
              search={searchQCardsAC}
              setPage={setPage}
            />
          </div>
        </div>
        <Paper sx={{ width: "80%", overflow: "hidden", margin: "auto" }}>
          <TableContainer sx={{ maxHeight: 600 }}>
            <Table stickyHeader aria-label="sticky table">
              <TableHead>
                <TableRow>
                  {columns.map((column) => (
                    <TableCell
                      onClick={
                        column.id === "Updated" ? HandleClickSort : undefined
                      }
                      key={v1()}
                      align={column.align}
                      style={{
                        minWidth: column.minWidth,
                        cursor: "pointer",
                        backgroundColor: " #EFEFEF",
                        fontWeight: "bold",
                      }}
                    >
                      {column.label}
                      {column.id === "Updated" ? (
                        <img
                          className={sort === "1grade" ? s.row : undefined}
                          src={row}
                          alt=""
                        />
                      ) : undefined}
                    </TableCell>
                  ))}
                </TableRow>
              </TableHead>
              <TableBody>
                {rows.map((row) => {
                  return (
                    <TableRow hover role="checkbox" key={v1()}>
                      {columns.map((column) => {
                        const value =
                          row.id !== userId
                            ? row[column.id] === row.Actions
                              ? (row.Actions = undefined)
                              : row[column.id]
                            : row[column.id];
                        return (
                          <TableCell key={v1()} align={column.align}>
                            {value}
                          </TableCell>
                        );
                      })}
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
          <TablePagination
            rowsPerPageOptions={[10, 25, 50]}
            component="div"
            count={cardsTotalCount}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </Paper>
      </>
    )
  ) : cards.length !== 0 ? (
    <>
      <NavLink
        onClick={() => dispatch(searchQCardsAC({ text: "" }))}
        className={s.back}
        to="/"
      >
        <ArrowCircleLeftOutlinedIcon /> <span>Back to Packs List</span>
      </NavLink>
      <div className={s.glav}>
        {" "}
        {packId === userId ? (
          <div>
            <span className={s.packs}>
              {namePack?.name}
              <Menushka />
            </span>{" "}
            <img src={cover?.deckCover} width={150} alt="" />
          </div>
        ) : (
          <div>
            <span className={s.packs}>{namePack?.name}</span>{" "}
            <img src={cover?.deckCover} width={150} alt="" />
          </div>
        )}
        {packId === userId ? (
          <ModalCard
            iconName={"Add New Card"}
            name={"Add New Card"}
            callbackOne={newCardTC}
          />
        ) : (
          <Button variant="contained">
            <NavLink className={s.nav} to={"/cards/" + url.id + "/learn"}>
              Learn to pack
            </NavLink>
          </Button>
        )}
      </div>
      <div className={s.panel}>
        <div className={s.search}>
          <span className={s.str}>Search</span>
          <SuperInput txt={searchQ} search={searchQCardsAC} setPage={setPage} />
        </div>
      </div>
      <Paper sx={{ width: "80%", overflow: "hidden", margin: "auto" }}>
        <TableContainer sx={{ maxHeight: 600 }}>
          <Table stickyHeader aria-label="sticky table">
            <TableHead>
              <TableRow>
                {columns.map((column) => (
                  <TableCell
                    onClick={
                      column.id === "Updated" ? HandleClickSort : undefined
                    }
                    key={v1()}
                    align={column.align}
                    style={{
                      minWidth: column.minWidth,
                      cursor: "pointer",
                      backgroundColor: " #EFEFEF",
                      fontWeight: "bold",
                    }}
                  >
                    {column.label}
                    {column.id === "Updated" ? (
                      <img
                        className={sort === "1grade" ? s.row : undefined}
                        src={row}
                        alt=""
                      />
                    ) : undefined}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map((row) => {
                return (
                  <TableRow hover role="checkbox" key={v1()}>
                    {columns.map((column) => {
                      const value =
                        row.id !== userId
                          ? row[column.id] === row.Actions
                            ? (row.Actions = undefined)
                            : row[column.id]
                          : row[column.id];
                      return (
                        <TableCell key={v1()} align={column.align}>
                          {value}
                        </TableCell>
                      );
                    })}
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[10, 25, 50]}
          component="div"
          count={cardsTotalCount}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
    </>
  ) : (
    <>
      <NavLink
        onClick={() => dispatch(searchQCardsAC({ text: "" }))}
        className={s.back}
        to="/"
      >
        <ArrowCircleLeftOutlinedIcon /> <span>Back to Packs List</span>
      </NavLink>
      <div className={s.glav}>
        {" "}
        {packId === userId ? (
          <div>
            <span className={s.packs}>
              {namePack?.name} {cards.length !== 0 ? <Menushka /> : undefined}
            </span>{" "}
            <img src={cover?.deckCover} width={150} alt="" />
          </div>
        ) : (
          <div>
            <span className={s.packs}>{namePack?.name}</span>{" "}
            <img src={cover?.deckCover} width={150} alt="" />
          </div>
        )}
        {packId === userId ? (
          <ModalCard
            iconName={"Add New Card"}
            name={"Add New Card"}
            callbackOne={newCardTC}
          />
        ) : undefined}
      </div>
      <div className={s.panel}>
        <div className={s.search}>
          <span className={s.str}>Search</span>
          <SuperInput txt={searchQ} search={searchQCardsAC} setPage={setPage} />
        </div>
      </div>
      <span className={s.spanZero}>
        There is no matching card for the given filter
      </span>
    </>
  );
}
