import axios from "axios";
// 'https://neko-back.herokuapp.com/2.0/'
// 'http://localhost:7542/2.0/'
const instance = axios.create({
  baseURL:
    process.env.REACT_APP_BACK_URL || "https://neko-back.herokuapp.com/2.0/",
  withCredentials: true,
});
export type Data = {
  email: string;
  password: string;
};
export type DataLogin = {
  email: string;
  password: string;
  rememberMe: boolean;
};
type ResponseRegistrationType = {
  addedUser?: {
    created: string;
    email: string;
    isAdmin: boolean;
    name: string;
    publicCardPacksCount: number;
    rememberMe: boolean;
    updated: string;
    verified: boolean;
    __v: number;
    _id: string;
    error?: string;
  };
};
type ResponseLoginType = {
  created: string;
  email: string;
  isAdmin: boolean;
  name: string;
  avatar?: string;
  publicCardPacksCount: number;
  rememberMe: boolean;
  updated: string;
  verified: boolean;
  __v: number;
  _id: string;
  error?: string;
};
export type Cards = {
  _id: string;
  cardsPack_id: string;
  user_id: string;
  question: string;
  answer: string;
  grade: number;
  shots: number;
  comments: string;
  type: string;
  rating: number;
  more_id: string;
  created: string;
  updated: string;
  __v: number;
  questionImg: string;
  answerImg: string;
};

export type CardsResponse = {
  cards: Cards[];
  packUserId: string;
  packName: string;
  packPrivate: boolean;
  packDeckCover: string;
  packCreated: string;
  packUpdated: string;
  page: number;
  pageCount: number;
  cardsTotalCount: number;
  minGrade: number;
  maxGrade: number;
  token: string;
  tokenDeathTime: number;
};

export type UpdatedGrade = {
  _id: string;
  card_id: string;
  user_id: string;
  cardsPack_id: string;
  grade: number;
  shots: number;
  more_id: string;
  created: string;
  updated: string;
  __v: number;
};

export type UpdatedGradeResponse = {
  updatedGrade: UpdatedGrade;
  token: string;
  tokenDeathTime: number;
};
export const registrationAuthLoginAPI = {
  registration(data: Data) {
    return instance.post<ResponseRegistrationType>("auth/register", data);
  },
  login(data: DataLogin) {
    return instance.post<ResponseLoginType>("auth/login", data);
  },
  auth() {
    return instance.post<ResponseLoginType>("auth/me");
  },
  logOut() {
    return instance.delete("auth/me");
  },
  name(name: string, avatar: string = "url or base64") {
    return instance.put("auth/me", { name, avatar });
  },
  avatar(avatar: string = "url or base64") {
    return instance.put("auth/me", { avatar });
  },
  packs(
    sort: string,
    pageCount?: number,
    page?: number,
    search?: string,
    min?: number,
    max?: number,
    id?: string
  ) {
    return instance.get(
      `cards/pack?pageCount=${pageCount}&page=${page}&sortPacks=${sort}&min=${min}&max=${max}&packName=${search}&user_id=${id}`
    );
  },
  cards(
    pageCount?: number,
    page?: number,
    sort?: string,
    min?: number,
    max?: number,
    id: string = "",
    searchQ?: string,
    searchA?: string
  ) {
    return instance.get<CardsResponse>(
      `cards/card?pageCount=${pageCount}&page=${page}&sortCards=${sort}&min=${min}&max=${max}&cardAnswer=${searchA}&cardsPack_id=${id}&cardQuestion=${searchQ}`
    );
  },
  newPacks(
    name: string = "no Name",
    value: boolean = false,
    avatar: string = "url or base64"
  ) {
    return instance.post("cards/pack", {
      cardsPack: {
        name,
        deckCover: avatar,
        private: value,
      },
    });
  },
  updatePack(
    id: string,
    name: string,
    value: boolean,
    avatar: string = "url or base64"
  ) {
    return instance.put("cards/pack", {
      cardsPack: {
        _id: id,
        name,
        deckCover: avatar,
        private: value,
      },
    });
  },
  newCard(
    id: string,
    question: string,
    answer: string,
    questionImg: string = "url or base 64",
    answerImg: string = "url or base 64"
  ) {
    return instance.post("cards/card", {
      card: {
        cardsPack_id: id,
        question,
        answer,
        grade: 0,
        shots: 0,
        answerImg,
        questionImg,
        questionVideo: "url or base 64",
        answerVideo: "url or base 64",
      },
    });
  },
  updateCard(
    id: string,
    question: string,
    answer: string,
    questionImg: string = "url or base 64",
    answerImg: string = "url or base 64"
  ) {
    return instance.put("cards/card", {
      card: {
        _id: id,
        question,
        answerImg,
        questionImg,
        answer,
      },
    });
  },
  grade(id: string, grade: number) {
    return instance.put<UpdatedGradeResponse>("cards/grade", {
      grade: grade,
      card_id: id,
    });
  },
  deletePacks(id: string) {
    return instance.delete(`cards/pack?id=${id}`);
  },
  deleteCard(id: string) {
    return instance.delete(`cards/card?id=${id}`);
  },
  forgot(email: string) {
    const promise = instance.post("auth/forgot", {
      email: email,
      from: "test-front-admin <ai73a@yandex.by>",
      message: `<div style="background-color: lime; padding: 15px">
password recovery link: 
<a href='https://igorka1996.github.io/first-big-project#/forgot/set-new-password/$token$'>
link</a>
</div>`,
    });
    return promise;
  },
  newPass(password: string, token: string) {
    const promise = instance.post("auth/set-new-password", {
      password: password,
      resetPasswordToken: token,
    });
    return promise;
  },
};
