import * as React from "react";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import SearchIcon from "@mui/icons-material/Search";
import { InputAdornment } from "@mui/material";
import { ChangeEvent, useEffect, useState } from "react";
import { useAppDispatch, useAppSelector } from "../store-reducers/store";

type PropsType = {
  setPage: (num: number) => void;
  search: (payload: { text: string }) => any;
  txt: string;
};
const defaultDebouncedTime = 1500;
export const useDebounce = (
  value: string,
  delay = defaultDebouncedTime
): string => {
  const [debouncedValue, setDebouncedValue] = useState<string>(value);
  useEffect(() => {
    const timer = setTimeout(() => setDebouncedValue(value), delay);
    return () => {
      clearTimeout(timer);
    };
  }, [value, delay]);
  return debouncedValue;
};
export const SuperInput = (props: PropsType) => {
  const status = useAppSelector((state) => state.errorReducer.status);
  const dispatch = useAppDispatch();
  const [value, setValue] = useState(props.txt);
  useEffect(() => {
    setValue(props.txt);
  }, [props.txt]);
  const debouncedValue = useDebounce(value);
  const debounce = (fn: any, ms: number) => {
    let timeout: ReturnType<typeof setTimeout>;
    return function (this: any, ...args: any[]) {
      clearTimeout(timeout);
      timeout = setTimeout(() => fn.apply(this, args), ms);
    };
  };
  const debounced = debounce((value: string) => {
    dispatch(props.search({ text: value }));
    props.setPage(0);
  }, 2000);
  const onChangeHandler = (
    e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    setValue(e.currentTarget.value);
    // console.log(e.currentTarget.value);
    // debounced(e.currentTarget.value);
    // dispatch(props.search({ text: debouncedValue }));
  };
  useEffect(() => {
    dispatch(props.search({ text: debouncedValue }));
  }, [debouncedValue]);

  return (
    <Box
      component="form"
      sx={{
        "& > :not(style)": { m: 1, width: "40ch" },
      }}
      noValidate
      autoComplete="off"
    >
      {/*<input*/}
      {/*  disabled={status === "loading"}*/}
      {/*  value={props.txt}*/}
      {/*  onChange={onChangeHandler}*/}
      {/*  type="text"*/}
      {/*/>*/}

      <TextField
        disabled={status === "loading"}
        onChange={onChangeHandler}
        value={value}
        style={{ height: "50px" }}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
              <SearchIcon />
            </InputAdornment>
          ),
        }}
        id="outlined-basic"
        label="Provide your text"
        variant="outlined"
      />
    </Box>
  );
};
