import React, { useEffect } from "react";
import "./App.css";
import { Header } from "./components/Header";
import { CardLogin } from "./components/CardLogin";
import { Navigate, Route, Routes, useLocation } from "react-router-dom";
import { ErrorSnackbar } from "./components/ErrorSnackbar";
import { LineProgressBar } from "./features/LineProgressBar";
import { useAppDispatch, useAppSelector } from "./store-reducers/store";
import { authTC } from "./store-reducers/registr-login-auth-reducer";
import { ForgotPassword } from "./components/ForgotPassword";
import { CheckEmail } from "./components/CheckEmail";
import { CreatePassword } from "./components/CreatePassword";
import { PersonalInfo } from "./components/PersonalInfo";
import { CircularProgress } from "@mui/material";
import { AllPacks } from "./components/AllPacks";
import { Cards } from "./components/Cards";
import { Learn } from "./components/Learn";

export function App() {
  const LineProgress = useAppSelector((state) => state.errorReducer.status);
  const initialize = useAppSelector((state) => state.errorReducer.initialize);
  const dispatch = useAppDispatch();
  const location = useLocation();
  const fromPage = location.state?.pathname || "/";

  useEffect(() => {
    dispatch(authTC());
  }, [dispatch]);

  if (!initialize) {
    return (
      <div
        style={{
          position: "fixed",
          top: "30%",
          textAlign: "center",
          width: "100%",
        }}
      >
        <CircularProgress />
      </div>
    );
  }
  if (initialize && fromPage !== "/") {
    return <Navigate to={fromPage} replace />;
  }

  return (
    <div className={"App"}>
      <Header />
      {LineProgress === "loading" ? <LineProgressBar /> : null}
      <Routes>
        <Route path={"/"} element={<AllPacks />} />
        <Route path={"/signin"} element={<CardLogin />} />
        <Route path={"/forgot"}>
          <Route index element={<ForgotPassword />} />
          <Route
            path={"set-new-password/:token"}
            element={<CreatePassword />}
          />
        </Route>
        <Route path={"/checkemail"} element={<CheckEmail />} />
        <Route path={"/personalinfo"} element={<PersonalInfo />} />
        <Route path={"/cards/:id"} element={<Cards />} />
        <Route path={"/cards/:id/learn"} element={<Learn />} />
      </Routes>
      <ErrorSnackbar />
    </div>
  );
}
