import { combineReducers, configureStore, ThunkAction } from "@reduxjs/toolkit";
import { registrationAuthLoginReducer } from "./registr-login-auth-reducer";
import thunkMiddleware from "redux-thunk";
import { errorReducer } from "./error-reducer";
import { packsReducer } from "./packs-reducer";
import { cardsReducer } from "./cards-reducer";
import { useDispatch, useSelector } from "react-redux";

const rootReducers = combineReducers({
  registrationAuthLoginReducer: registrationAuthLoginReducer,
  errorReducer: errorReducer,
  packsReducer: packsReducer,
  cardsReducer: cardsReducer,
});

export const store = configureStore({
  reducer: rootReducers,
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware().prepend(thunkMiddleware),
});

export type AppDispatch = typeof store.dispatch;
export const useAppDispatch = () => useDispatch<AppDispatch>();
export type AppRootStateType = ReturnType<typeof rootReducers>;
export const useAppSelector = <T>(fn: (state: AppRootStateType) => T): T => {
  return useSelector(fn);
};
//@ts-ignore
window.store = store;
export type AppTaskType<ReturnType = void> = ThunkAction<
  ReturnType,
  AppRootStateType,
  unknown,
  any
>;
